import React from "react";
import style from './RepoInfo.module.css'
import { UserData } from '../../Home/Home';
import { useLocation } from 'react-router-dom';
import { v4 as uuid } from 'uuid';
const RepoInfo = (prop) => {
    const id = useLocation().pathname.split('/')[1];

    const user = UserData.find(user => user.github_id === id);
    const repos = user.repos;

    return (<div className={style["main_repo-container"]}>
        <h1 className={style.heading}>Github repositories</h1>
        <hr className={style.divider}></hr>

        {

            repos && repos.map(repo => {
                return (<div key={uuid()} className={style["repo-info"]}>

                    <a className={style.link} href={repo.html_url}>{repo.name}</a>
                    <img className={style.arrow} src="./images/north_east-24px.svg" alt="arrowhead" />
                    <span className={style.updateTime}>{repo.updated_at}</span>
                    <p className={style.description}>{repo.description}</p>
                    <hr className={style.divider}></hr>
                </div>)
            })

        }

    </div>);
}


export default RepoInfo;