import React from "react";
import { Link } from "react-router-dom";
import style from './Header.module.css'


const Header = () => {
    return (
        <div className={style.header}>
            <h1>The Developer Profile</h1>
            <Link to="/">
                <h1>All Developers</h1>
            </Link>
        </div>
    );
};

export default Header;