import React, { useState, useEffect, useRef } from "react";
import style from "./Form.module.css";




const Form = (prop) => {
    const firstInput = useRef(null);

    useEffect(() => {
        firstInput.current.focus();
    })
    const [formData, setFormData] = useState({
        github_id: "",
        linkedin_id: "",
        codechef_id: "",
        hackerrank_id: "",
        twitter_id: "",
        medium_id: ""
    });

    const handleChange = (e) => {
        setFormData({
            ...formData,
            [e.target.name]: e.target.value
        });
    };
    const handleClick = (e) => {
        e.stopPropagation();
    }
    const removeForm = () => {
        prop.onClick()
    }

    return (
        <div onClick={handleClick} className={style["form-container"]}>
            <h2 className={style["form-title"]}>Add developer profile</h2>
            <form className={style.form} onSubmit={(e) => {
                prop.handleSubmit(formData, e);
                removeForm();
            }}>
                <hr className={style["horizontal-rule"]} />
                <div className={style.label}>
                    <label htmlFor="github_id">
                        <img className={style.image} src="./images/profile-icons/github.png" alt="Logo" />
                        <span className={style.title}>GitHub*</span>
                    </label>
                </div>
                <input ref={firstInput} type="text" className={style["input-field"]} onInput={handleChange} id="github_id" name="github_id" placeholder="Enter your GitHub id" required />

                <div className={style.label}>
                    <label htmlFor="linkedin_id">
                        <img className={style.image} src="./images/profile-icons/linkedin.png" alt="Logo" />
                        <span className={style.title}>LinkedIn</span>
                    </label>
                </div>
                <input onInput={handleChange} type="text" className={style["input-field"]} id="linkedin_id" name="linkedin_id" placeholder="Enter your LinkedIn id" />

                <div className={style.label}>
                    <label htmlFor="codechef_id">
                        <img className={style.image} src="./images/profile-icons/codechef.png" alt="Logo" />
                        <span className={style.title}>Codechef</span>
                    </label>
                </div>
                <input onInput={handleChange} type="text" className={style["input-field"]} id="codechef_id" name="codechef_id" placeholder="Enter your Codechef id" />

                <div className={style.label}>
                    <label htmlFor="hackerrank_id">
                        <img className={style.image} src="./images/profile-icons/hackerrank.png" alt="Logo" />
                        <span className={style.title}>Hackerrank</span>
                    </label>
                </div>
                <input onInput={handleChange} type="text" className={style["input-field"]} id="hackerrank_id" name="hackerrank_id" placeholder="Enter your Hackerrank id" />

                <div className={style.label}>
                    <label htmlFor="twitter_id">
                        <img className={style.image} src="./images/profile-icons/github.png" alt="Logo" />
                        <span className={style.title}>Twitter</span>
                    </label>
                </div>
                <input onInput={handleChange} type="text" className={style["input-field"]} id="twitter_id" name="twitter_id" placeholder="Enter your Twitter id" />

                <div className={style.label}>
                    <label htmlFor="medium_id">
                        <img className={style.image} src="./images/profile-icons/github.png" alt="Logo" />
                        <span className={style.title}>Medium</span>
                    </label>
                </div>
                <input onInput={handleChange} type="text" className={style["input-field"]} id="medium_id" name="medium_id" placeholder="Enter your Medium id" />

                <hr className={style["horizontal-rule"]} />
                <span onClick={removeForm} className={style.cancel}>Cancel</span>
                <button className={style.submit} type="submit">Submit</button>
            </form>
        </div >
    );
}

export default Form;
