import React, { useState } from 'react';

import Header from './HeaderMain/Header';
import DevDetails from './DevDetail/DevDetails';
import Button from './Button/Button';
import Form from './Form/Form';
import Footer from '../../components/UI/Footer/Footer';
import BackDrop from '../../components/UI/BackDrop/BackDrop';

import './Home.css';
import axios from "axios";

import DevDetail from 'data/DevDetail';
import UserData from 'data/UserData';

const Home = () => {
    const [isClicked, setIsClicked] = useState(false);
    const [devDetails, setDevDetails] = useState([]);

    const handleSubmit = async (formData, e) => {
        e.preventDefault();
        try {
            const { data } = await axios.post(process.env.REACT_APP_BACKEND_URL, formData, {
                headers: { 'Content-Type': 'application/json' }
            });
            setDevDetails(prevDevDetails => {
                DevDetail.push(data);
                return [...prevDevDetails, data];
            });

            const response = await axios.get(`${process.env.REACT_APP_BACKEND_URL}${formData.github_id}`);
            UserData.push(response.data);
        } catch (err) {

            alert("Inavlid Username");
        }
    }


    const formOpenHandler = () => {
        setIsClicked(true);
    }
    const backDropCloseHandler = () => {
        setIsClicked(false);
    }
    const formCancelClickHandler = () => {
        setIsClicked(false);
    }

    return <>
        {isClicked && <BackDrop onClick={backDropCloseHandler} >
            <Form handleSubmit={handleSubmit} onClick={formCancelClickHandler} />
        </BackDrop>}


        <Header title="The Developer Repository" src="/images/dev.png" alt="Hero" />
        <p className="heading">Explore developer Profiles</p>
        <hr />

        <DevDetails currDetails={devDetails} details={DevDetail} />
        <hr />
        <p className="question">Could not find what you were looking for?</p>
        <Button onClick={formOpenHandler} text="Add developer info" />
        <Footer />
    </>
}

export default Home;
export { UserData };
