import React from 'react';
import style from './ProfileBanner.module.css';
import { UserData } from '../../Home/Home';
import { useLocation } from 'react-router-dom';

const ProfileBanner = (prop) => {
    const id = useLocation().pathname.split('/')[1];

    const user = UserData.find(user => user.github_id === id);


    return (
        <div className={style.banner}>
            <div className={style.image}>
                <img src={user.avatar_url || './images/account.svg'} alt="banner" />
            </div>
            <div className={style.banner_info}>
                <div className={style.name}>
                    <h1>{user.name}</h1>
                </div>
                <div className={style.bio}>
                    <p>{user.bio}</p>
                </div>
                <div className={style["image-container"]}>
                    <a href={`https://github.com/${user.id}`} target="_blank" rel="noopener noreferrer">
                        <img className={style.logo} src="./images/profile-icons/github.png" alt="avatar" />
                    </a>
                    <a href={`https://www.hackerrank.com/${user.hackerrank_id}`} target="_blank" rel="noopener noreferrer">
                        <img className={style.logo} src="./images/profile-icons/hackerrank.png" alt="avatar" />
                    </a>
                    <a href={`https://www.codechef.com/users/${user.codechef_id}`} target="_blank" rel="noopener noreferrer">
                        <img className={style.logo} src="./images/profile-icons/codechef.png" alt="avatar" />
                    </a>
                    <a href={`https://www.linkedin.com/in/${user.linkedin_id}/`} target="_blank" rel="noopener noreferrer">
                        <img className={style.logo} src="./images/profile-icons/linkedin.png" alt="avatar" />
                    </a>
                    <a href={`https://medium.com/${user.medium_id}`} target="_blank" rel="noopener noreferrer">
                        <img className={style.logo} src="./images/profile-icons/medium.png" alt="avatar" />
                    </a>
                    <a href={`https://twitter.com/${user.twitter_id}`} target="_blank" rel="noopener noreferrer">
                        <img className={style.logo} src="./images/profile-icons/twitter.png" alt="avatar" />
                    </a>
                </div>
                <div className={style["extra-info"]}>
                    <div><img src="./images/location.svg" alt="" /><small>{user.location}</small> </div>
                    <div><img src="./images/business.svg" alt="" /><small>{user.company}</small> </div>
                    <div>
                        <a href="https://workat.tech" target="_blank" rel="noopener noreferrer">
                            <img src="./images/insertlink.svg" alt="" />
                        </a> <small>{user.email}</small>

                    </div>
                </div>
            </div>
        </div >
    );
}
export default ProfileBanner;