import React, { useState } from 'react';
import style from './DevDetails.module.css'
import { Link } from 'react-router-dom';
import { v4 as uuidv4 } from 'uuid';
import axios from 'axios';

let searchItem = [];
let user;
const DevDetails = (props) => {
    const [input, setInput] = useState("");
    const [searchComplete, setSearchComplete] = useState(false);
    const [showAllDev, setShowAllDev] = useState(true);

    const handleInput = (e) => {
        const id = e.target.value;
        setInput(id);
        if (id === "" || id === null) {
            setShowAllDev(true);
            setSearchComplete(false);
        }
    }

    const handleChange = async (e) => {
        e.preventDefault();
        try {
            const { data } = await axios.get(`${process.env.REACT_APP_BACKEND_URL}${input}`)
            searchItem[0] = { id: data.id, "avatar_url": data["avatar_url"] };
            if (searchItem.length !== 0) {
                user = searchItem[0];
                setShowAllDev(false);
                setSearchComplete(true);
            }
        } catch (err) {
            console.log(err);
        }
    }

    return (
        <>
            <div className={style.search}>
                <form className={style.form} onSubmit={handleChange} >
                    <input className={style["input-search"]} onInput={handleInput} value={input} type="text" placeholder="Search for username"></input>
                    <button className={style.btn}><img className={style["btn-search"]} src="/images/search-24px.svg" alt="Search icon" /></button>
                </form>
            </div>
            <div className={style["dev-details"]}>
                {showAllDev && props.details.map((detail, index) => {
                    return (
                        <div key={uuidv4()} className={style["dev-description"]}>
                            <img className={style.image} src={detail.avatar_url || './images/account.svg'} alt="Avatar" />
                            <h3 className={style.description}>{detail.id}</h3>
                            <Link className={style.link} to={detail.id}>
                                <img src="./images/north_east-24px.svg" alt="Arrow Head" />
                            </Link>
                        </div>
                    )
                })}
                {
                    searchComplete && <div className={style["dev-description"]}>
                        <img className={style.image} src={user["avatar_url"] || './images/account.svg'} alt="Avatar" />
                        <h3 className={style.description}>{user.id}</h3>
                        <Link className={style.link} to={user.id}>
                            <img src="./images/north_east-24px.svg" alt="Arrow Head" />
                        </Link>
                    </div>

                }
            </div>
        </>
    );
}

export default DevDetails;
