
import React from "react";
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';



import Developer from "./pages/Developer/Devloper";
import Home from './pages/Home/Home'


const App = () => {
    return (
        <Router>
            <Switch>
                <Route path="/" exact>
                    <Home />
                </Route>
                <Route path="/:id" >
                    <Developer />
                </Route>
            </Switch>
        </Router>
    )

}

export default App;